import React, { useState } from "react";

import './editorArea.css'
import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/eclipse.css';

import 'codemirror/mode/python/python.js'
import 'codemirror/mode/htmlmixed/htmlmixed.js'
import 'codemirror/mode/javascript/javascript.js'
import 'codemirror/mode/css/css.js'
import 'codemirror/mode/clike/clike.js'
import 'codemirror/mode/brainfuck/brainfuck.js'






//const userColors = ["#5cc8ff", "#ff3c38", "#fff275", "#92bfb1", "#ff8c42", "#649a47", "#343633", "#7d70ba", "#dec1ff", "#114b5f"];

let codemirror = require('codemirror');


export default class CodeMirror extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            visibility: 'hidden'
        }


        this.options = {
            lineNumbers: true,
            fullScreen: false,
            theme: "eclipse",
            mode: 'htmlmixed',
            smartIndent: false,
            undoDepth: 0,
            height: "100%",
            width: "100%",
            "font-size": "30px",
            extraKeys: {
                "Enter": function (cm) {
                    cm.replaceSelection("\n");
                }
            }
        };


    }

    getFileLanguage(fileName) {
        let ret = ""
        let temp = fileName.split('.')
        if (temp.length > 1) { //if there's an extension
            let extension = temp[temp.length - 1].toLowerCase();
            if (extension == 'py') {
                ret = 'python';
            }
            else if (extension == 'c') {
                ret = 'text/x-csrc';
            }
            else if (extension == 'cpp') {
                ret = 'text/x-c++src';
            }
            else if (extension == 'java') {
                ret = 'text/x-java';
            }
            else if (extension == 'cs') {
                ret = 'text/x-csharp';
            }
            else if (extension == 'html') {
                ret = 'htmlmixed'
            }
            else if (extension == 'js') {
                ret = 'javascript';
            }
            else if (extension == 'css') {
                ret = 'css'
            }
            else if (extension == 'bf') {
                ret = 'brainfuck'
            }
        }
        return ret //if there's is no extension or the extension is not supported
    }

    componentDidMount() {
        this.createEditor();
        this.initServerOnEvents()
    }






    initServerOnEvents() {

        this.props.server.on("user-left-file", user => {
            this.removeUserBookmark(user.id)
        })

        this.props.server.on("get-file-cursers", userToPos => {
            this.cleanUsersInFile()
            for (let id in userToPos) {
                if (id !== this.props.myDetails.id) {
                    this.props.usersInFile[id] = {};
                    this.props.usersInFile[id]["color"] = userToPos[id].user.color;
                    this.props.usersInFile[id]['userCurser'] = this.createBookmark(userToPos[id].pos, this.props.usersInFile[id]["color"])
                }
            }
        })

        this.props.server.on("curser-moved", data => {
            if (data.user.id !== this.props.myDetails.id) {
                if (!this.props.usersInFile[data.user.id]) { //if a new user has entered
                    this.props.usersInFile[data.user.id] = {}
                    this.props.usersInFile[data.user.id]["color"] = data.user.color;
                }
                else {
                    this.props.usersInFile[data.user.id].userCurser.clear()
                }
                this.props.usersInFile[data.user.id]["userCurser"] = this.createBookmark(data.currPos, this.props.usersInFile[data.user.id].color)
            }
        })

        this.props.server.on('get-file-contents', (file) => {
            this.setState({ visibility: 'visible' })
            this.editor.setOption('mode', this.getFileLanguage(file.name)) //setting the file mode
            this.editor.doc.setValue(file.data)
        })

        this.props.server.on('update-file', data => {

            this.addToEditorAtIndex(data.payload, data.start, data.end);
        });

        this.props.server.on("paste-action", data => {
            this.addToEditorAtIndex(data.payload, data.start, data.end);
        });


        this.props.server.on('kick-from-file',()=>{
            this.props.fileDetails.name = ""
            this.props.fileDetails.path = ""
            this.state.visibility = "hidden"
            this.forceUpdate()
        })

    }



    createEditor() {
        this.editor = codemirror(this.ref, this.options)
        this.editor.setSize(null, "100%");
        this.editor.doc.setValue('Wating for file data from the server...\n<div></div>')

        this.editor.on('cursorActivity', (event) => {
            let start = this.editor.doc.getCursor(true);
            let end = this.editor.doc.getCursor(false);

            this.props.server.emit('curser-moved', { 'start': start, 'end': end });
        });

        this.editor.on("beforeChange", (cm, event) => {
            if (event.origin === "paste") {
                event.text = event.text.join('\n')
                this.textPasted(event);
                event.cancel();
            }
            else if (event.origin === "+delete") {

                this.textChanged(event);
                event.cancel();
            }
            else if (event.origin === "+input") {
                //checking if enter was pressed
                if (Array.isArray(event.text) && event.text.length > 1) {
                    event.text = '\n'
                }
                this.textChanged(event);
                event.cancel();
            }
        })

    }

    render() {
        let margin_left = parseInt(this.props.windowDetails.leftBar.width, 10) + parseInt(this.props.windowDetails.pullStripe.width, 10) + 50 + "px"

        if (this.props.fileDetails.name == "") {
            //defalt page goes here
            this.state.visibility = "hidden"
        }
        else {
            this.state.visibility = "visible"
        }

        return [
            <div style={{ background: "url('/cloud_backrunde.png')", width:`100%`, top: "68px", bottom: "0px", position: "fixed", zIndex: "-9999" }}></div>,
            <div style={{ width: "100%", visibility: this.state.visibility, top: "68px", bottom: "0px", position: "absolute", zIndex: "0", "margin-left": margin_left }}>
                <div style={{ height: "100%", "font-size": "20px" }} ref={(self) => this.ref = self} />
            </div>]

    }

    addToEditorAtIndex(text, start, end = start) {
        this.editor.doc.replaceRange(text, start, end);
    }

    textPasted(event) {
        let pastedText = event.text;

        let selectionStart = event.from;
        let selectionEnd = event.to;
        this.props.server.emit('paste-action', { "payload": pastedText, "start": selectionStart, "end": selectionEnd });
    }

    textChanged(event) {
        let keyPressed = event.text;
        let selectionStart = event.from;
        let selectionEnd = event.to;


        this.props.server.emit('update-file', { "payload": keyPressed, "start": selectionStart, "end": selectionEnd });
    }


    //---------------------------------------------------------------------------------------------------------------------

    createBookmark(cursorPos, color) {
        let marker = null;
        if (cursorPos.start.line === cursorPos.end.line && cursorPos.start.ch === cursorPos.end.ch) {
            const cursorCoords = this.editor.cursorCoords(cursorPos.end);
            const cursorElement = document.createElement('span');
            cursorElement.style.borderLeftStyle = 'solid';
            cursorElement.style.borderLeftWidth = '2px';
            cursorElement.style.borderLeftColor = color;
            cursorElement.style.height = `${(cursorCoords.bottom - cursorCoords.top)}px`;
            cursorElement.style.padding = 0;
            cursorElement.style.zIndex = 0;
            marker = this.editor.setBookmark(cursorPos.end, { widget: cursorElement });
        }
        else {
            marker = this.editor.markText(cursorPos.start, cursorPos.end, { "css": 'background-color:' + color })
        }
        return marker;
    }



    //function getRandomInt(max) {
    //	return Math.floor(Math.random() * Math.floor(max));
    //}

    /*
    Function adds the given text in between the start and end indexes of the current editor element text.
    */


    cleanUsersInFile() {
        for (let user in this.props.usersInFile) {
            this.removeUserBookmark(user);
        }
    }

    removeUserBookmark(user) {
        try { //sometimes reloading the page causes errors when deleting bookmarks.
            this.props.usersInFile[user].userCurser.clear();
            delete this.props.usersInFile[user];
        }
        catch (err) {

        }
    }

    print_pos(pos) {
        return `{ "line": ${pos.line}, "ch": ${pos.ch}}`
    }

    print_msg(msg) {
        return `{ "payload": ${msg.payload}, "start": ${this.print_pos(msg.start)}, "end": ${this.print_pos(msg.end)}}`
    }
}


