import React, { Component } from 'react'
import './topBar.css'
import { BrowserRouter as Router, Route, Link , Redirect} from "react-router-dom";
import cookie from "../../clientModules/cookies"

import 'react-tippy/dist/tippy.css'
import {Tooltip} from 'react-tippy';  

export default class TopBar extends React.Component {
    constructor(props) {
        super(props); 
      
        this.state = {"redirect":null} 
        
        this.optionsVisibale = "hidden"
        this.usersVisibale = "hidden"
        this.codeVisibale = "hidden"
    }

    componentDidMount(){

        if (!this.props.explorer){
            this.usersTags = []
            this.props.server.emit("get-users-in-project");
            this.props.server.emit("generate-project-code");

            this.props.server.on("get-users-in-project",(users)=>{
                this.usersTags = []
                for (let user of users){  
                    let username = user.username === this.props.myDetails.username ? "You" : user.username
                    
                    this.usersTags.push(
                    <Tooltip class="tippy-tooltip" title={username} position="bottom" style={{"z-index" : "99999"}}>
                        <img src={user.imageUrl} id="users-imgs" class="top-bar-users-in-project" style={{"box-shadow": `0px 0px 0px 2px ${user.color}`}}></img>

                    </Tooltip>)
                }
                this.forceUpdate()
            })

            
            this.props.server.on("relode-user-in-project",()=>{
                this.props.server.emit("get-users-in-project")
            })

            this.props.server.on('generate-project-code', code =>{
                this.setState({"code":code})
            })
        }

        document.addEventListener('mousedown', (event) =>{
            if(event.toElement.id == "top-bar-user-btn"){
                this.optionsVisibale = this.optionsVisibale == "visible"? "hidden":"visible"
                this.usersVisibale = "hidden"
                this.codeVisibale = "hidden"
            }
            else if (event.toElement.id == "top-bar-users-button-id"){
                    this.optionsVisibale = "hidden"
                    this.usersVisibale = this.usersVisibale == "visible"? "hidden":"visible"
                    this.codeVisibale = "hidden"
                
            }
            else if(event.toElement.id == "top-bar-code-btn"){
                this.optionsVisibale = "hidden"
                this.usersVisibale = "hidden"
                this.codeVisibale = this.codeVisibale == "visible"? "hidden":"visible"
            }
            else if(! ["disconect-code-btn","option-bubble","users-imgs","copy-code-btn","users-bubble", "code-bubble","project-code"].includes(event.toElement.id)){
                this.optionsVisibale = "hidden"
                this.usersVisibale = "hidden"
                this.codeVisibale = "hidden"
            }
            this.forceUpdate()
        })

        
    }

    copyCode() {
        let temp = document.getElementById("test")
        temp.select()
        document.execCommand("copy");
    }



    render() { 
        let comp =null
        if (this.props.explorer){
            comp = [
                <img src="/logo_and_name.png" class="top-bar-logo-and-name noselect"></img>,
                <div class="search noselect">
                    <div class="search-icon material-icons w3-xxlarge noselect">search</div>
                    <input type="text" placeholder="Search" onChange={(e)=>{

                        this.props.searchPhrase.searchPhrase = e.target.value
                        
                        this.props.renderPerent()
                    }} />
                </div>
            ]
        }
        else{
            let usersTagsLen = 1
            try{
                usersTagsLen = this.usersTags.length
                if (usersTagsLen > 5){
                    usersTagsLen = 5
                }
            }
            catch(err){ }
            comp = [
                <Tooltip title="Go To Project Explorer" position="bottom" style={{position:"absolute",top:"7px"}}>
                    <img src="/logo1.png" class="top-bar-logo noselect" style={{cursor: "pointer"}} onClick={()=>{this.setState({'redirect' : "/projectExplorerPage"})}}></img>
                </Tooltip>,
                <span class="top-bar-project-name">{this.props.myDetails.currProject.split("@")[0]}</span>,
                <div id="top-bar-users-button-id" class="top-bar-users-button noselect material-icons w3-xxlarge" onClick={()=>{this.props.server.emit("get-users-in-project")}}>supervisor_account</div>,
                <div id="users-bubble" class="users-bubble" style={{visibility: this.usersVisibale, left:`calc(100% - 150px - ${usersTagsLen*(46/2)}px)`}}>
                    {this.usersTags}
                </div>

            ]
            if(this.state.code){
                 comp.push(
                    <div class="top-bar-code-btn material-icons w3-xxlarge noselect" id="top-bar-code-btn">person_add</div>,
                    <div class="code-bubble" id="code-bubble" style={{visibility: this.codeVisibale}}>
                        <div class="project-code" id="project-code">{this.state.code}</div>
                        <button class="copy-code-btn" id="copy-code-btn" onClick={() => {this.copyCode()}}>Copy Project Code</button>
                    </div>,
                    <input id="test" value={this.state.code} style={{position:'fixed',top:"100000px", height:"1px",width:"1px",border:"0px",padding:"0px", "z-index": -9999}}></input>
                )
            }
        }


        if(!this.state.redirect){
            return(
                <div class="top-bar"> 
                    {comp}
                    <img src={this.props.myDetails.imageUrl} id="top-bar-user-btn" class="top-bar-user-btn"></img>
                    <div id="option-bubble" class="option-bubble" style={{visibility: this.optionsVisibale}}>
                        <button class="disconect-code-btn" id="disconect-code-btn" onClick={() => {cookie.deleteAll();this.setState({'redirect' : "/loginPage"})}}>disconnect</button>
                    </div>
                </div>
            );
        }
        
        else{
            if(this.state.redirect === '/loginPage'){
                this.optionsVisibale = "hidden"
                this.props.server.emit('logout')
            }
            else if(this.state.redirect === "/projectExplorerPage"){
                this.optionsVisibale = "hidden"
                this.props.server.emit("user-left-project")
                this.props.server.emit("relode-user-in-project")
            }
            return(
                <Redirect to={this.state.redirect}/>
            )
        }
    }
    

}


