import React from "react"
import './App.css';
import { BrowserRouter as Router, Route, Link, Redirect } from "react-router-dom";
import io from "socket.io-client";

import EditorPage from './client/clientPages/editorPage'
import ProjectExplorerPage from './client/clientPages/projectExplorerPage'
import LoginPage from './client/clientPages/loginPage'
import cookie from "../src/client/clientModules/cookies"



let server = io();

//relogin if the server caught an exception



let usersInFile = {};

let myDetails = { username: "", fullName: "", id: "", imageUrl: "", currProject: "" };

const debug = true;

export default class App extends React.Component {
    constructor(props) {
        super(props)
        this.state = { serverRun: false }
        server.emit("dos-server-run")
    }


    componentDidMount() {
        server.on("dos-server-run", () => {
            if (this.state.serverRun != true) {
                this.setState({ serverRun: true })
            }
        })

        server.on('force-logout', () => {
            cookie.deleteAll()
            document.getElementById("move-to-login-page").click()
        })

        window.addEventListener('beforeunload', (event) => {
            event.preventDefault()
            usersInFile = {};
            myDetails = { username: "", fullName: "", id: "", imageUrl: "", currProject: "" };
            server.emit("logout")
            document.getElementById("move-to-login-page").click()
        });

    }

    render() {
        if (this.state.serverRun) {
            return [
                <a id="move-to-login-page" href="/"></a>,
                <Router>
                    <Route path="/" component={() => <Redirect to={"/loginPage"} />} />

                    <Route path="/loginPage" component={() => { server.emit("dos-server-run"); return <LoginPage server={server} myDetails={myDetails} debug={debug} /> }} />
                    <Route path="/projectExplorerPage" component={() => { server.emit("dos-server-run"); return <ProjectExplorerPage server={server} myDetails={myDetails} previousPage={'/loginPage'}></ProjectExplorerPage> }} />

                    <Route path="/editorPage" component={() => { server.emit("dos-server-run"); return <EditorPage server={server} myDetails={myDetails} usersInFile={usersInFile} previousPage={'/projectExplorerPage'}></EditorPage> }} />
                </Router>
            ];
        }
        else {
            return [<h1>error 404</h1>, <h2>server is offline</h2>]
        }
    }
}
