
import React from "react"
import CodeMirror from '../clientComponents/editor/editorArea'
import LeftBar from '../clientComponents/leftBar/leftBar'
import TopBar from '../clientComponents/topBar/topBar'
import { BrowserRouter as Router, Route, Link , Redirect} from "react-router-dom";
import toastr from "../clientModules/toastr"


let windowDetails = {
    "leftBar":{width:"200px", minWidth:"170px"},
    "pullStripe":{width:"5px"}
}

let fileDetails = {name:"", path:""}


export default class EditorPage extends React.Component{

    constructor(props){
        super(props)
        this.state = {"redirect":null}
        this.startMode = {mode:"fileTree"}
    }

    componentDidMount(){
 
        this.props.server.on("error",(msg)=>{
            this.props.server.emit("logout")
            this.setState({"redirect":"/projectExplorerPage"})
            this.props.server.emit("connect-user-to-server",  this.props.myDetails)
            toastr.error(msg)
        
        })
        

    }


    render(){

        if(this.state.redirect){
            return <Redirect to={this.state.redirect}/>
        }
        else{

            return [
                <TopBar server={this.props.server} myDetails={this.props.myDetails} previousPage={this.props.previousPage} usersInFile={this.props.usersInFile} fileDetails={fileDetails}></TopBar> ,
                <div style={{width:"100%", height:"100"}}> 
                    <LeftBar server={this.props.server} myDetails={this.props.myDetails} windowDetails={windowDetails} fileDetails={fileDetails} renderPerent={()=>{this.forceUpdate()}} mode={this.startMode}></LeftBar>
                    <CodeMirror server={this.props.server} myDetails={this.props.myDetails} usersInFile={this.props.usersInFile} windowDetails={windowDetails} fileDetails={fileDetails}/>
                </div>
            ]
        }
    }
}




