import React from "react"
import './login.css'
import { GoogleLogin } from 'react-google-login'
import { Redirect } from "react-router-dom";
import cookie from "../../clientModules/cookies"



export default class Login extends React.Component {

    constructor(props) {
        super(props)
        this.state = { redirect: "" }
    }

    googleButton() {
        return (
            <div>
                <GoogleLogin
                    clientId="966024770692-9ja2b8bbd4mjda9f326ev2umc7tqtd1k.apps.googleusercontent.com"
                    buttonText="Login with google"
                    onSuccess={this.responseGoogle}
                    onFailure={this.responseGoogle}
                    cookiePolicy={'single_host_origin'}
                />
            </div>
        )
    }

    responseGoogle = (response) => {
        this.props.myDetails.id = response.profileObj.email.split('@')[0];
        this.props.myDetails.username = response.profileObj.givenName;
        this.props.myDetails.fullName = response.profileObj.name;
        this.props.myDetails.imageUrl = response.profileObj.imageUrl;


        cookie.create("id", this.props.myDetails.id)
        cookie.create("username", this.props.myDetails.username)
        cookie.create("fullName", this.props.myDetails.fullName)
        cookie.create("imageUrl", this.props.myDetails.imageUrl)

        this.props.server.emit("connect-user-to-server", this.props.myDetails)
        this.setState({ redirect: "/projectExplorerPage" })
    }

    readLoginCookies() {
        this.props.myDetails.id = cookie.read('id');
        this.props.myDetails.username = cookie.read('username');
        this.props.myDetails.fullName = cookie.read('fullName');
        this.props.myDetails.imageUrl = cookie.read('imageUrl');
        return this.props.myDetails.id != "" && this.props.myDetails.username != "" && this.props.myDetails.fullName != ""
    }


    render() {
        if (this.readLoginCookies()) {
            this.props.server.emit("connect-user-to-server", this.props.myDetails)
            return (<Redirect to={"/projectExplorerPage"} />)
        }
        else if (!this.state.redirect) {

            let debugLoginUsers = []

            if (this.props.debug) {
                debugLoginUsers = [
                    <br />,
                    <br />,
                    <button class="signin-button" onClick={() => { this.responseGoogle({ "profileObj": { "email": "ericashdod@gmail.com", "givenName": "Erikku", "name": "Erikku Raskin", "imageUrl": "https://lh3.googleusercontent.com/a-/AOh14GhQSRkfdmk64uEjDNyFAq6nczf9Gkoeomp2a3liDw=s96-c" } }) }}>Login as Erikku</button>,
                    <br />,
                    <br />,
                    <button class="signin-button" onClick={() => { this.responseGoogle({ "profileObj": { "email": "lior.sheva103@gmail.com", "givenName": "Lior", "name": "Lior Shahar - Ben Ami", "imageUrl": "https://lh3.googleusercontent.com/a-/AOh14GgkF6XSSh1UWJOMY49b7Qh0dTRZNbG454S4evU6=s96-c" } }) }}>Login as Lior</button>,
                    <br />,
                    <br />,
                    <button class="signin-button" onClick={() => { this.responseGoogle({ "profileObj": { "email": "ofs@gmail.com", "givenName": "Ofer", "name": "Ofer Segev", "imageUrl": "https://lh3.googleusercontent.com/a-/AOh14GhLG4J0tiTyS2Oi2Xc_cSM9UcbwE6KS3xqHKf1T=s96-c" } }) }}>Login as Ofer</button>
                ]
            }


            return [
                debugLoginUsers,
                <div class="login-container noselect">
                    <div class="login-div" >
                        <div class="logo"></div>
                        <div class="title">Olympus Project</div>
                        <div class="sub-title">Brought to you by Liorik's</div>
                        <br />
                        <br />
                        <button id="signin-google-button" class="signin-google-button">{this.googleButton()}</button>
                        <button class="signin-button" onClick={() => { document.getElementById("signin-google-button").getElementsByTagName("button")[0].click() }}>
                            <img class="google-icon" src="/g-normal.png" width="40" height="40" />
                            Login with google
                        </button>

                    </div>
                </div>
            ]
            //<button class="signin-button">Login</button>
        }
        else {
            return (<Redirect to={this.state.redirect} />)

        }

    }
} 