import React, { Component } from 'react'
import { Redirect, useHistory, BrowserRouter as Router, Route, Link } from "react-router-dom";
import './projectOptions.css';
import MyPopup from "../myPopup/myPopup"

export default class ProjectOptions extends React.Component {
    constructor(props) {
        super(props);
        this.state = { rerender: null, popup: "" }
    }

    createProject(projectName) {
        if (projectName) {
            this.props.server.emit('create-project', projectName)
            this.setState({ "rerender": "/editorPage", popup: "" })
        }
        else {
            this.setState({ "rerender": null, popup: "" })
        }
    }

    joinProject(projectName) {
        if (projectName) {
            this.props.server.emit("get-project-file-tree", projectName);
            this.setState({ "rerender": "/editorPage", popup: "" })
        }
        else {
            this.setState({ "rerender": null, popup: "" })
        }
    }




    render() {
        if (this.state.rerender !== null) {
            return (<Redirect to={this.state.rerender} />)
        }
        else {
            return [
                this.state.popup,
                <div class="project-options noselect">

                    <div class="setup-btn" onClick={() => { this.setState({ rerender: null, popup: <MyPopup handleClose={(value) => {if(!value){this.setState({ rerender: null, popup:""})}}} Text="Insert the code you got from the project creator" Title="Enter project code:" DefaultText="project code" ErrorText="Error: invalid project code" ValidText={(code) => this.isLegalProjectCode(code)} capsLock={true}></MyPopup> }) }}>
                        <img class="Sicon" src="/conect.png" width="40" height="40" />
                        <div class="Sname">join existing project</div>
                        <div class="Sdits">join an exisitng project with a special project key</div>
                    </div>


                    <div class="setup-btn" onClick={() => { this.setState({ rerender: null, popup: <MyPopup handleClose={(value) => { this.createProject(value) }} Text="Choose a name for your project:" Title="Enter project name:" DefaultText="project name" ErrorText="Error: invalid project name" ValidText={this.isLegalProjectName}></MyPopup> }) }}>
                        <img class="Sicon" src="/add.png" width="40" height="40" />
                        <div class="Sname">create a new project</div>
                        <div class="Sdits">create a new project and start having fun coding!</div>
                    </div>

                </div>
            ]
        }

    }

    selectProject(selectedItem) {
        this.server.emit("get-project-file-tree", selectedItem);
        this.setState({ "rerender": "/editorPage", popup: "" })
    }

    isLegalProjectName(fileName) {
        
        //if string is made up only of spaces
        if (fileName.replace(" ", "").length == 0) {
            return false;
        }
        // if string contains \/:*?"<>|@ 
        else if (fileName.includes('\\') || fileName.includes('/') || fileName.includes(':') || fileName.includes('*') || fileName.includes('?') || fileName.includes('"') || fileName.includes('<') || fileName.includes('>') || fileName.includes('|') || fileName.includes('@') || fileName == "") {
            return false;
        }

        return true;

    }

    isLegalProjectCode(code) {
        this.props.server.emit('enter-project', code)
        this.props.server.on("enter-project", projectName => {
            console.log(projectName)
            if (!projectName) {
                return false
            }
            else {
                this.props.myDetails.currProject = projectName;
                this.joinProject(projectName)
                return true
            }
        });
        return false

    }
}


