import React, { Component, Fragment } from "react";
import ReactDOM from "react-dom";
import Tree from "react-ui-tree";
import cx from "classnames";
import Icon from "react-icons-kit";
import { folder } from "react-icons-kit/feather/folder";
import { file } from "react-icons-kit/feather/file";
import { folderPlus } from "react-icons-kit/feather/folderPlus";
import { filePlus } from "react-icons-kit/feather/filePlus";
import { chevronsRight } from "react-icons-kit/feather/chevronsRight";
import { chevronsDown } from "react-icons-kit/feather/chevronsDown";
import styled from "styled-components";
import { ContextMenu, MenuItem, ContextMenuTrigger } from "react-contextmenu";
import _ from "lodash";
import { StrollableContainer } from "react-stroller";
import deepdash from "deepdash";

import CodeMirror from '../editor/editorArea'
import MyPopup from "../myPopup/myPopup"

import "./styles.css";
import "react-ui-tree/dist/react-ui-tree.css";
import "./theme.css";
import "./react-contextmenu.css";

// add deepdash to lodash
deepdash(_);

function collect(props) {
    return props;
}

function deleteFromTree(o, id) {
    function getNode(a, i) {
        if (a.id === id) {
            index = i;
            return true;
        }
        if (Array.isArray(a.children) && a.children.some(getNode)) {
            if (~index) {
                a.children.splice(index, 1);
                index = -1;
            }
            return true;
        }
    }

    var index = -1;
    [o].some(getNode);
}

const initialState = {
    active: null,
    tree: {},
    collapsed: false // start with unmodified tree
};

class TreeFile extends Component {

    state = initialState;
    constructor(props) {
        super();
        props.fileDetails.name = ""
    }

    componentDidMount() {
        this.props.server.on('get-project-file-tree', data => {
            this.setState({ tree: data })
        })

        this.props.server.on('file-created', data => {
            this.addItem("file", { id: data.path }, data.name)
        })

        this.props.server.on('folder-created', data => {
            this.addItem("folder", { id: data.path }, data.name)
        })

        this.props.server.on('delete-path', path => {
            deleteFromTree(this.state.tree, path);
            this.setState({
                tree: this.state.tree
            }
            );

            if (this.props.fileDetails.path == path) {
                this.props.fileDetails.path = ""
                this.props.fileDetails.name = ""
                this.props.renderPerent();
            }
        })
    }

    renderNode = node => {
        const renderFileFolderToolbar = (isFolder, caption) => (
            <Toolbar onClick={isFolder ? null : () => this.sendGetContentsRequest(node.id)}>
                <FloatLeft>
                    <Icon icon={isFolder ? folder : file} />
                    {caption}
                </FloatLeft>
                <ToolbarFileFolder>
                    {isFolder && (
                        <Fragment>
                            <Icon
                                title="New Folder"
                                icon={folderPlus}
                                onClick={() => this.addItem("folder", node)}
                            />
                            <Icon
                                title="New File"
                                icon={filePlus}
                                onClick={() => this.addItem("file", node)}
                            />
                        </Fragment>
                    )}
                </ToolbarFileFolder>
            </Toolbar>
        );

        /*const attributes = {
            "data-count": 0,
            className: "example-multiple-targets well"
        };*/

        const isFolder = node.hasOwnProperty("children");
        return (
            <ContextMenuTrigger
                id="FILE_CONTEXT_MENU"
                key={node.id}
                name={node.id}
                collect={collect}
                holdToDisplay={-1}
                onItemClick={this.handleContextClick}
            >
                {renderFileFolderToolbar(isFolder, node.module)}
            </ContextMenuTrigger>
        );
    };

    isLegalFileName(children, fileName) {

        //if string is made up only of spaces
        if (fileName.replace(" ", "").length == 0) {
            return false;
        }
        // if string contains \/:*?"<>|@ 
        else if (fileName.includes('\\') || fileName.includes('/') || fileName.includes(':') || fileName.includes('*') || fileName.includes('?') || fileName.includes('"') || fileName.includes('<') || fileName.includes('>') || fileName.includes('|') || fileName.includes('@') || fileName == "") {
            return false;
        }
        else {
            for (var child of children) {
                if (child.module === fileName) {
                    return false;
                }
            }
        }
        return true;

    }



    addItem = (itemType, active, fileName = '') => {
        const { tree } = this.state;

        let createFile = (name) => {
            if (name) {
                if (fileName === '') {
                    if (itemType == "file") {
                        this.props.server.emit('create-file', {
                            "path": active.id,
                            "name": name
                        })
                    }
                    if (itemType == "folder") {
                        this.props.server.emit('create-folder', {
                            "path": active.id,
                            "name": name
                        })
                    }
                }

                const newItem =
                    itemType === "folder"
                        ? {
                            id: active.id + "/" + name,
                            module: name,
                            children: [],
                            collapsed: false
                        }
                        : {
                            id: active.id + "/" + name,
                            leaf: true,
                            module: name
                        };

                const newTree = _.mapDeep(tree, (item, key, parentValue) => {
                    const cloneItem = Object.assign({}, item);
                    if (cloneItem) {
                        if (cloneItem.id === active.id && cloneItem.children) {
                            // folder
                            cloneItem.children.push(newItem);
                        }
                    }
                    return cloneItem;
                });

                this.setState({ ...newTree });
            }
            this.props.popup.popup = ""
            this.forceUpdate()
        }

        if (fileName === '') { //if the user is creating the file
            this.props.popup.popup = <MyPopup handleClose={createFile} Text={`choose name for the ${itemType} your abaout to creat`} Title={`Enter ${itemType} name:`} DefaultText={`${itemType} name`} ErrorText={`Error: invalid ${itemType} name`} ValidText={(name) => { return this.isLegalFileName(active.children, name) }}></MyPopup>
            this.forceUpdate()
        }
        else {
            createFile(fileName)
        }

    }

    handleContextClick = (e, { action, name: id }) => {
        const { tree } = this.state;

        switch (action) {
            case "rename":
                const renameObj = _.findDeep(tree, (item) => item.id === id, {
                    childrenPath: "children"
                });

                let folderId = id.split("/")
                folderId.pop()
                if (folderId.length > 1) {
                    folderId = folderId.join("/")
                }
                else {
                    folderId = this.state.tree.id
                }


                const my_dir = _.findDeep(tree, (item) => item.id === folderId, {
                    childrenPath: "children"
                });


                let sendRename = (name) => {
                    if (name) {
                        this.props.server.emit('rename-path', { objectPath: id, newName: name })
                    }
                    this.props.popup.popup = ""
                    this.forceUpdate()
                }

                this.props.popup.popup = <MyPopup handleClose={sendRename} Text={`choose a new name for your ${renameObj.value.module}`} Title={`Enter a new ${renameObj.value.module} name:`} DefaultText={`${renameObj.value.module} name`} ErrorText={`Error: invalid ${renameObj.value.module} name`} ValidText={(name) => { this.isLegalFileName(my_dir.value.children, name) }}></MyPopup>
                this.forceUpdate()

                break;
            case "delete":
                if (id != this.state.tree.id) {
                    this.props.server.emit("delete-path", id)
                    deleteFromTree(tree, id);
                    this.setState({
                        tree
                    });
                    this.props.fileDetails.path = ""
                    this.props.fileDetails.name = ""
                    this.props.renderPerent();
                }
                break;
            default:

        }
    };

    toggleCollapse = () => {
        this.setState(({ collapsed }) => ({ collapsed: !collapsed }));
    };
    // just for the demo to get the initialState back
    // ---> not working & not sure why
    resetState = () => {
        // this.setState({ tree: {} });
        this.setState({ tree: Object.assign({}, initialState.tree) });
    };


    sendGetContentsRequest = (filePath) => {
        this.props.server.emit('get-file-contents', filePath)
        this.props.fileDetails.name = filePath.split("/")[filePath.split("/").length - 1]
        this.props.fileDetails.path = filePath;
        this.props.renderPerent()
    };


    render() {
        const { collapsed } = this.state;

        const ret = [
            this.props.popup.popup,
            <div class="noselect">
                <div id="leftPullStripe" style={this.props.pullStripeStyle} />
                <div className="tree" style={{ width: this.props.windowDetails.leftBar.width, "--file-tree-width": this.props.windowDetails.leftBar.width, "--strip-width": this.props.pullStripeStyle.width }}>
                    <Toolbar>
                        <FloatLeft>

                            <span class="noselect">{this.props.fileDetails.name}</span>
                        </FloatLeft>
                    </Toolbar>
                    {!collapsed && (
                        <StrollableContainer draggable bar={LightScrollbar}>
                            <Tree
                                paddingLeft={20}
                                tree={this.state.tree}
                                onChange={this.handleChange}
                                renderNode={this.renderNode}
                            />
                        </StrollableContainer>
                    )}
                </div>


                <ContextMenu id="FILE_CONTEXT_MENU">
                    {/* Add copy / cut later */}
                    {/* <MenuItem data={{ action: "copy" }} onClick={this.handleContextClick}>
                    Copy
                    </MenuItem>
                    <MenuItem divider /> */}

                    <MenuItem
                        data={{ action: "rename" }}
                        onClick={this.handleContextClick}
                    >
                        Rename
                    </MenuItem>

                    <MenuItem
                        data={{ action: "delete" }}
                        onClick={this.handleContextClick}
                    >
                        Delete
                    </MenuItem>
                </ContextMenu>
            </div>
        ];

        return ret;
    }
    //
    handleChange = (tree) => {
        let changed = findChangedFile(tree.children, tree.id);
        console.log('fileTree:', tree)
        console.log(changed);
        if (changed) {
            this.props.server.emit('file-tree-move-item', changed)
        }

        this.setState({
            tree: tree
        });
    };
}

const LightScrollbar = styled.div`
        width: 10px;
        background-color: #fff;
        opacity: 0.7;
        border-radius: 4px;
        margin: 4px;
    `;
const Toolbar = styled.div`
        position: relative;
        display: flex;
        color: #d8e0f0;
        z-index: +1;
        /*border: 1px solid white;*/
        padding-bottom: 4px;
        i {
        margin-right: 5px;
        cursor: pointer;
        }
        i :hover {
        color: #d8e0f0;
        }
    `;

const FloatLeft = styled.span`
        padding-left: 4px;
        width: 100%;
    `;

const ToolbarFileFolder = styled.div`
        position: absolute;
        text-align: right;
        width: 92%;
        color: transparent;
        &:hover {
        color: #d8e0f0;
        }
    `;

export default TreeFile


function addNode(prop, value, parent) {
    parent[prop] = value;
}

function isArray(obj) {
    return (Object.prototype.toString.call(obj) === '[object Array]');
}

function findChangedFile(fileTree, path) {
    console.log(fileTree)
    console.log(path)
    for (let obj of fileTree) {
        console.log(obj)

        if (obj.id !== path + "/" + obj.module) {
            console.log(obj)
            return {
                "oldPath": obj.id,
                "newPath": path + "/" + obj.module
            };

        }
        else if (!obj.leaf) { //if folder
            console.log("!!!!!!!!!!!", obj)
            let ans = findChangedFile(obj.children, obj.id)
            if (ans) {
                return ans;
            }
        }
    }
    return false;

}