import zIndex from '@material-ui/core/styles/zIndex';
import { set } from 'lodash';
import React, { Component } from 'react'
import FileTree from '../fileTree/fileTree'
import './leftBar.css'

export default class LeftBar extends React.Component {
    constructor(props) {
        super(props);

        
        this.moseDown = false

    }


    componentDidMount() {
        

        document.addEventListener('mousedown', (event) =>{
            if (event.toElement.id == "leftPullStripe"){
                this.moseDown = true
            }
        })

        document.addEventListener('mousemove', (event) =>{
            if (this.moseDown == true){
                if(parseInt(this.props.windowDetails.leftBar.minWidth,10) < event.x-50){
                    this.props.windowDetails.leftBar.width = event.x -50 +"px"
                }
                else{
                    this.props.windowDetails.leftBar.width = parseInt(this.props.windowDetails.leftBar.minWidth,10) +"px"
                }
                this.props.renderPerent()
            }
        })

        document.addEventListener('mouseup', (event) =>{
            this.moseDown = false
        })


    }

    
    setFileTreeState() {

        this.props.mode.mode = "fileTree"
        this.forceUpdate()
    }

    setVoiceVideoState() {
        this.props.mode.mode = "voiceVideo"
        this.forceUpdate()
    }


    render() {
        let pullStripeStyle = {
            position: "absolute",
            background: "#ebebeb",
            width: this.props.windowDetails.pullStripe.width,
            bottom:"0px",
            left: parseInt(this.props.windowDetails.leftBar.width,10)+50 + "px",
            top: "68px",
            cursor: "ew-resize",
            zIndex:"999"
        }

        let fileTreeV = this.props.mode.mode === "fileTree" ? 'visible':"hidden";
        let voiceVideoV = this.props.mode.mode === "voiceVideo" ? 'visible':"hidden";
        


        return[
    
            <div class="left-bar">
                <div class="file-tree-state-btn" style={{visibility:"hidden"}} onClick={()=>{this.setFileTreeState()}}>FT</div>
                <div class="voic-video-state-btn" style={{visibility:"hidden"}} onClick={()=>{this.setVoiceVideoState()}}>VV</div>
            </div>,
            <div style={{visibility:fileTreeV}}>
                <FileTree server={this.props.server} windowDetails={this.props.windowDetails} pullStripeStyle={pullStripeStyle} fileDetails={this.props.fileDetails} popup={{popup:""}} renderPerent={this.props.renderPerent}/>,
            </div>,
            <div style={{visibility:voiceVideoV}}>
                <div id="leftPullStripe" style={pullStripeStyle}/>
                <div style={{position:"absolute", width:this.props.windowDetails.leftBar.width , background:"#ff0000",left: "50px",bottom:"0px",top:"68px"}}></div>
            </div>
        ];

    }

}


