import { update } from "lodash"
import React from "react"
import ProjectList from "../clientComponents/projectList/projectList"
import ProjectOptions from "../clientComponents/projectOptions/projectOptions"
import TopBar from "../clientComponents/topBar/topBar"
import "./projectExplorerPage.css"
import toastr from "../clientModules/toastr"






export default class ProjectExplorerPage extends React.Component{

    constructor(props){
        super(props)

        this.searchPhrase = {searchPhrase:""}
    }

    componentDidMount(){
        this.props.server.on("error",(msg)=>{
            this.props.server.emit("logout")
            this.props.server.emit("connect-user-to-server",  this.props.myDetails)
            toastr.error(msg)
        })

    }

    renderPerent(){
        this.forceUpdate()
    }

    render(){
        return [
            <TopBar server={this.props.server} myDetails={this.props.myDetails} page={"ProjectChoosingPage"} previousPage={this.props.previousPage} explorer={true} searchPhrase={this.searchPhrase} renderPerent={()=>{this.renderPerent()}}></TopBar>,
            <div class="project-related" >
                <ProjectList server={this.props.server} myDetails={this.props.myDetails} searchPhrase={this.searchPhrase}/>
                <ProjectOptions server={this.props.server} myDetails={this.props.myDetails}/>
            </div>
        ];
    }
}