import toastr from "toastr";
import "toastr/build/toastr.min.css";

export default class Toastr{

    static error(msg){
        toastr.options = {
            positionClass: "toast-bottom-right",
            hideDuration: 300,
    
            timeOut: 60000,
            progressBar: true
        };
        toastr.clear();
        setTimeout(() => toastr.error(msg), 300);
    }

    static success(msg){
        toastr.options = {
            positionClass: "toast-bottom-right",
            hideDuration: 300,
            timeOut: 60000,
            progressBar: true
        };
        toastr.clear();
        setTimeout(() => toastr.success(msg), 300);
    }

    static warning(msg){
        toastr.options = {
            positionClass: "toast-bottom-right",
            hideDuration: 300,
            timeOut: 60000,
            progressBar: true
        };
        toastr.clear();
        setTimeout(() => toastr.warning(msg), 300);
    }

}