import React, { Component } from 'react'
import { Redirect ,useHistory, BrowserRouter as Router, Route, Link } from "react-router-dom";
import './projectList.css';
import MyPopup from "../myPopup/myPopup"
import { update } from 'lodash';

export default class ProjectList extends React.Component {
    constructor(props) {
        super(props);
        this.server = props.server;

        this.state = {"projects": [], "displayedProjects":[], "serverAnswered":false , "rerender": null, "popup":null}

        this.server.emit("get-projects-list")
               
    }

    componentDidMount() {
        this.visibleOption = ""

        this.server.on('get-projects-list', (projects) => {
            let temp = []
            for (let project of projects){
                let date = project.creationDate.split("T")[0].split("-").reverse().join(".")
                temp.push({ id: project.id, name: project.projectName, creator: project.creator, date: date})
            }
            this.setState({"projects":temp, "displayedProjects":temp, "serverAnswered":true});
        });

        this.server.on("update-projects-list",()=>{
            this.server.emit("get-projects-list")
        })

        document.addEventListener("click",(event)=>{
            if (event.target.classList.value != "rename" && event.target.classList.value != "clone" && event.target.classList.value != "delete" && event.target.classList.value != "options-container" && event.target.classList.value != "options" && event.target.classList.value != "options-icon"){
                this.visibleOption = ""
                this.forceUpdate()
            }
        })

    }

    onSearchInput(event){
        let newList = []
        this.state.projects.forEach(project => {
            if(project.name.includes(event.target.value)){
                newList.push(project)
            }
        });
        this.setState({"projects":this.state.projects, "displayedProjects":newList, "serverAnswered" : true})
    }
    
    initProjectsList(){

        let projects = []
        if(this.state.displayedProjects){ //if the server sent a response on the get-project-list request
            this.state.displayedProjects.forEach(project => {
                if(project.name.toLowerCase().includes(this.props.searchPhrase.searchPhrase.toLowerCase()))
                {
                    let renameButton = null
                    if(project.id.split("@")[1] == this.props.myDetails.id){
                        renameButton = <a class="rename" onClick={()=>{this.setState({...this.state, popup: <MyPopup handleClose={(value) => { this.renameProject(project.id,value) }} Text="Choose a new name for your project:" Title="Enter new project name:" DefaultText="new project name" ErrorText="Error: invalid new project name" ValidText={this.isLegalProjectName}></MyPopup>})}}> rename</a>
                    }
                    projects.push(
                    <div class="btn noselect" onClick={(e)=> {if(e.target.classList.value == "btn noselect" || e.target.classList.value == "app-icon"  || e.target.classList.value == "Pname" || e.target.classList.value == "Pcreator"  || e.target.classList.value == "Pdate"){this.selectProject(project)}}}>
                        <img class="app-icon" src="/logo1.png" width="40" height="40" />
                        <div class="Pname">{project.name}</div>
                        <div class="Pcreator">{project.creator}</div>
                        <div class="Pdate">{project.date}</div>
                        <div class="options" onClick={()=>{this.visibleOption = project.id; this.forceUpdate()}}> 
                            <p class="options-icon">&#x2807;</p>
                            <div class="options-container" style={this.optionsContainerStyle(project.id)}>
                                {renameButton}
                                <a class="clone" onClick={()=>{this.cloneProject(project.id)}}>clone</a>
                                <a class="download" href={`/download?project=${project.id}`}>download</a>
                                <a class="delete" onClick={()=>{this.deleteProject(project.id)}}>delete</a>
        
                            </div>
                        </div>
                    </div>)
                }
            })
        }
        else if(this.state.serverAnswered){ //if the user has no projects to display
            projects = [
                <h1>No projects to display!</h1>,
                <h2>you can create some of your own</h2>,
                <h2>or join an existing one with a code</h2>
            ]

        }
        
        return projects
    }

    optionsContainerStyle(id){
        if(this.visibleOption==id){
            return {"visibility": "visible"}
        }
        else{
            return {"visibility": "hidden"}
        }
    }

    cloneProject(projectId){
        this.server.emit('clone-project', projectId);
    }
    
    deleteProject(projectId){
        this.server.emit('delete-project', projectId);
    }

    renameProject(projectId,newProjectName){
        if (newProjectName) {
            this.props.server.emit('rename-project', projectId, newProjectName);
        }
        this.setState({...this.state, popup: "" })
    }

    
    

    render() {
        let projects = this.initProjectsList()

        if(this.state.rerender !== null){
            return (<Redirect to={this.state.rerender}/>)
        }
        else{
            return [
                <div class="container noselect">
                    <div class="seperetor-container">
                        <div class="Pname-tag">name</div>
                        <div class="Pcreator-tag">creator</div>
                        <div class="Pdate-tag">date</div>  
                    </div>   
                    <div class="seperetor"></div>  
                    <div class="list-container noselect">
                        <div class="btn-container"></div>
                        {projects}
                    </div> 
                </div> ,
                this.state.popup
            ]
        }

    }

    selectProject(selectedItem){
        this.server.emit("get-project-file-tree", selectedItem.id);

        this.props.myDetails.currProject = selectedItem.id
        this.setState({"rerender": "/editorPage"})
    }

    isLegalProjectName(fileName) {

        //if string is made up only of spaces
        if (fileName.replace(" ", "").length == 0) {
            return false;
        }
        // if string contains \/:*?"<>|@ 
        else if (fileName.includes('\\') || fileName.includes('/') || fileName.includes(':') || fileName.includes('*') || fileName.includes('?') || fileName.includes('"') || fileName.includes('<') || fileName.includes('>') || fileName.includes('|') || fileName.includes('@') || fileName == "") {
            return false;
        }
        return true;

    }


}


