import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';



export default class MyPopup extends React.Component {
    constructor(props) {
        super(props)

        this.state = { error: false, helperText: "" }
    }

    componentDidMount() {
        document.addEventListener("keypress", (e) => {
            if (e.key === 'Enter') {
                try {
                    document.getElementById("Submit-Button").click()
                } catch { }

            }
        })
    }

    submit() {
        let value = this.refs.valueOf().myField.getElementsByClassName("MuiInputBase-input MuiInput-input MuiInputBase-inputMarginDense MuiInput-inputMarginDense")[0].value
        if (this.props.ValidText(value) == false) {
            this.setState({ error: true, helperText: this.props.ErrorText })
        }
        else {
            this.setState({ error: false, helperText: "" })
            this.props.handleClose(value)
        }
    }

    render() {
        return (
            <div>
                <Dialog open={true} onClose={() => { this.props.handleClose(false) }} aria-labelledby="form-dialog-title" autoComplete="off">
                    <DialogTitle id="form-dialog-title">{this.props.Title}</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            {this.props.Text}
                        </DialogContentText>
                        <TextField
                            autoFocus
                            error={this.state.error}
                            margin="dense"
                            id="name"
                            label={this.props.DefaultText}
                            type="email"
                            ref="myField"
                            helperText={this.state.helperText}
                            onChange={() => {
                                if (this.props.capsLock) {
                                    let value = this.refs.valueOf().myField.getElementsByClassName("MuiInputBase-input MuiInput-input MuiInputBase-inputMarginDense MuiInput-inputMarginDense")[0].value
                                    this.refs.valueOf().myField.getElementsByClassName("MuiInputBase-input MuiInput-input MuiInputBase-inputMarginDense MuiInput-inputMarginDense")[0].value = value.toUpperCase();
                                }
                                this.setState({ error: false, helperText: "" })
                            }}
                            fullWidth
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => { this.props.handleClose(false) }} color="primary">
                            Cancel
                    </Button>
                        <Button id="Submit-Button" onClick={() => { this.submit() }} color="primary">
                            Submit
                    </Button>
                    </DialogActions>
                </Dialog>
            </div>
        );
    }
}




